.client-review{
  background: url("../img/client-review__bg.png");
  padding-top: 124px;
  padding-bottom: 154px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
    &__title{
      margin-bottom: 58px;
    }
    &__slider{
      margin-bottom: 50px;
    }
    &__pagination{
      text-align: center;
      .swiper-pagination-bullet{
        width: 20px;
        height: 20px;
        background: #fff;
        opacity: 1;
          &:not(:last-child){
            margin-right: 10px;
          }
          &.swiper-pagination-bullet-active{
            background: var(--main);
          }
      }
    }
}
.review-slide{
  text-align: center;

    &__img{
      margin-bottom: 30px;
    }
    &__name{
      font-size: 24px;
      font-family: var(--Poppins);
      font-weight: 600;
      margin-bottom: 30px;
    }
    &__text{
      max-width: 878px;
      margin: auto;
      font-family: var(--Poppins);
    }
}